import './index.css';
import { NavLink } from 'react-router-dom';

export default function Nav(){
    const manue = document.querySelector(".nav_menue");
    const men_btn = document.querySelector("#open_btn");
    const clos_btn = document.querySelector("#close_btn");

    const handleClick =() => {
            manue.style.display = "flex";
        }
        const close =() => {   
            manue.style.display="none";   
        }
    return(
        <>
          <nav >
        <div className="container nav_container">
            <h4>NUTRITION</h4>
             <ul className="nav_menue">
                 <li><NavLink to="/Bmi" >Home</NavLink></li>
                 <li><NavLink to="/Form">BMI</NavLink></li>
                 <li><NavLink to="/Contect">Contect</NavLink></li>
             </ul>
             <button  id="open_btn" onClick={handleClick}>=</button>
           
             <button id="close_btn" onClick={close} >X</button>
        </div>
    </nav>
        </>
    )
}