import './style.css';
import './index.css';
import Nav from './Nav';
import { useState } from 'react';
import Footer from './Footer';


export default function Form(){
const [weight,setWeight] = useState(0);
const [height,setHeight] = useState(0);
const [bmi,setBmi] = useState("");
const [statuse,setStatuse] = useState("");
const [percentage,setPercentage] = useState("");
const [extra,setExtra] = useState("");

    let calculateBmi = (event) => {
   
        event.preventDefault();
        if(weight ===0 && height===0){
          alert("Invalid Input!")
        }
        else{
          let bmi=(weight/(height/100)** 2)
          setBmi(bmi.toFixed(2))
          const normalBMI = 24.9;
          const  percentage = (bmi-normalBMI);
           const  extra=(percentage*100/normalBMI);
      
          if(bmi<18.5){
            setStatuse("you are underweight")
          }
          else if(bmi>=18.5 && bmi<=24.9){
            setStatuse("you are healthy")
      
          }
          else if(bmi>=24.9 && bmi<=29.9){
            setStatuse("you are overweught")
            setPercentage(percentage.toFixed(2));
            setExtra(extra.toFixed(2))
          }
          else{
            setStatuse("you are obese")
            setPercentage(percentage.toFixed(2));
            setExtra(extra.toFixed(2))
          }
        }
      } 

return(
    <>
 <Nav/>
    {/* ****************************FORM******************************** */}
    <section className="sec">
    <div className="container container-hero">
        <h1>BMI Calculator</h1>
    <form onSubmit={calculateBmi}>
      <div>
      <label>Weight (Kg)</label>
      <input value={weight} onChange = {(e) => setWeight(e.target.value)} />
      </div>
      <div>
      <label>Height (Cm)</label>
      <input value={height} onChange = {(e) =>setHeight(e.target.value)} />
      </div>
      <button type='submit' className='btn'>Submit</button>
    </form>
    <div className='bmi-result'>
      <h3>Your BMI is : {bmi}</h3>
      <p className='statuse'>{statuse}</p>
      <p >your fatness percentage as your BMI is :{percentage}%</p>
      <p>your extra weight is :{extra}Kg</p>
    </div>
</div>
    </section>
    
{/* <!--**********************************FOOTER**************************************************--> */}
   <Footer/>
    </>

);

}