import './contect.css';
import Nav from './Nav';
import './index.css';
import Footer from './Footer';

export default function Contect(){
    let contect = require('./imgs/contect.jpg');

    return(
        <>
 <Nav/>

    {/* <!--*****************************************************--> */}
    <section class="contect">
        <div class="container contect_container">
            <aside class="contect_aside">
                <div class="aside_image">
                    
                    <img class="con" src={contect} />
                </div>
                <h2>Contect Us</h2>
                <p>Your suggestions and opinions are valuable for me and it shows your 
                    support so share your opinions with me by sending message.</p>
                <ul class="contect_details">
                    <li>
                        <h5>+93 747545191</h5>
                    </li>
                    <li>
                        <h5>sarinasultani515@gmail.com</h5>
                    </li>
                    <li>
                        <h5>Location</h5>
                    </li>
                </ul>
                
            </aside>
            <form action="https://formspree.io/f/moqzqonr"  method="POST" class="contect_form">
                <div class="form_name">
                    <input  type="text" name="First Name" placeholder="First Name" required />
                    <input type="text" name="Last Name" placeholder="Last Name" required />

                </div>
                <input className='email' type="email" name="Email Address" placeholder="Email Address"required />
                <textarea name="Message"  placeholder="Message" rows="7"></textarea>
                <button type="submit" class="btn primary_btn">Send Message</button>
            </form>
        </div>
    </section>

    {/* <!--**********************************FOOTER**************************************************--> */}
    <Footer />
        </>

    );

}